.interface {
  position: absolute;
  height: 100%;
  width: 100%;
  max-width: 1600px;
  max-height: 1200px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.interface > .bg {
  z-index: -10;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,.25);
  position: fixed;
  top: 0;
  left: 0;
}

.interface-header {
  height: 50px;
  width: 100%;
  -ms-flex-positive: 0;
      flex-grow: 0;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  padding: 16px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}

.interface-content {
  height: 100%;
  width: 100%;
  -ms-flex-positive: 1;
      flex-grow: 1;
  display: -ms-flexbox;
  display: flex;
}

.tile-col { width: 50% }

.tile-container {
  border-radius: 10px;
  padding: 8px;
  -webkit-transform: translate3d(0,0,0);
          transform: translate3d(0,0,0);
}

.tile-col.left .tile-container { padding-left: 16px }
.tile-col.right .tile-container { padding-right: 16px }
.tile-col :last-child { padding-bottom: 16px }

.tile-container .tile {
  height: 100%;
  width: 100%;
  padding: 20px;
  background: rgba(0,0,0,.2);
  -webkit-box-shadow: 0px 0px 31px 0px rgba(0,0,0,0.1);
          box-shadow: 0px 0px 31px 0px rgba(0,0,0,0.1);
  border-radius: 10px;
}

#portfolio, #balances, #volume { height: 33.3333333% }
#breakdown { height: 45% }
#charts { height: 55% }


