* {
  outline: none;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  color: white;
  width: 100%;
  height: 100vh;
  background: rgba(20,207,204,1);
  background: -webkit-linear-gradient(315deg, rgba(20,207,204,1), rgba(56,101,173,1), rgba(130,69,150,1));
  background: -o-linear-gradient(315deg, rgba(20,207,204,1), rgba(56,101,173,1), rgba(130,69,150,1));
  background: linear-gradient(135deg, rgba(20,207,204,1), rgba(56,101,173,1), rgba(130,69,150,1));
}

#root {
  height: 100vh;
  width: 100vw;
}

.app {
  height: 100vh;
  width: 100vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

 @media screen and (orientation:landscape) {
  .app {
    height: 100vw;
    width: 100vh;
    -webkit-transform: translateX(-100%) rotate(-90deg);
        -ms-transform: translateX(-100%) rotate(-90deg);
            transform: translateX(-100%) rotate(-90deg); /* W3C */  
    -webkit-transform-origin: right top;  
        -ms-transform-origin: right top;  
            transform-origin: right top;
  }
}

.unlock {
  height: 100vh;
  width: 100vw;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 10;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background: rgba(0,0,0,.6);
  opacity: 0;
  overflow: hidden;
}
.unlock .input {
  text-align: center;
  -webkit-text-security: disc;
  cursor: text;
  font-size: 2em;
  letter-spacing: 1em;
  color: white;
  padding-left: 1em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  z-index: 10;
}
.mobile .unlock .input {
  font-size: 1em;
}
.unlock .input:empty:before {
  content: 'pass';
  opacity: .2;
  margin-left: 1em;
}
.unlock .input:after {
  content: '';
  display: block;
  height: 100vh;
  width: 100vw;
  position: fixed;
  left: 0;
  top: 0;
  z-index: -1;
}
.unlock .logo {
  width: 40vw;
  margin-bottom: 50px;
}
 .unlock-error {
  position: absolute;
  bottom: 15%;
  cursor: pointer;
  -webkit-transition: opacity 150ms;
  -o-transition: opacity 150ms;
  transition: opacity 150ms;
  background: rgba(255,255,255,.1);
  padding: .75em 1.5em;
  font-size: 1em;
  border-radius: .5em;
  z-index: 20;
}
.unlock-error:hover { opacity: .8 }
.unlock-error.hide { opacity: 0 }
@media only screen and (max-width: 767px) {
  .unlock .input {
    font-size: 1.2em;
    letter-spacing: .5em;
    padding-left: .5em;
  }
  .unlock .input:empty:before {
    margin-left: .5em;
  }
  .unlock .logo {
    margin-bottom: 25px;
  }
}


